import React, { Component } from "react";
import Select from "antd/lib/select";
import Form from "antd/lib/form";

const Option = Select.Option;

// Data source: https://en.wikipedia.org/wiki/States_and_federal_territories_of_Malaysia
const malaysiaStates = [
  {
    value: "Johor Darul Ta'zim",
    text: "Johor Darul Ta'zim"
  },
  {
    value: "Kedah Darul Aman",
    text: "Kedah Darul Aman"
  },
  {
    value: "Kelantan Darul Naim",
    text: "Kelantan Darul Naim"
  },
  {
    value: "Melaka",
    text: "Melaka"
  },
  {
    value: "Negeri Sembilan Darul Khusus",
    text: "Negeri Sembilan Darul Khusus"
  },
  {
    value: "Pahang Darul Makmur",
    text: "Pahang Darul Makmur"
  }
];

let timeout;
let currentValue;

function fetch(value, callback) {
  if (timeout) {
    clearTimeout(timeout);
    timeout = null;
  }
  currentValue = value;

  function fake() {
    if (currentValue === value) {
      callback(malaysiaStates.filter(state => state.text));
    }
  }

  timeout = setTimeout(fake, 300);
}

class SearchInStateSelect extends Component {
  state = {
    data: [],
    value: ""
  };
  handleChange = value => {
    this.setState({ value });
    fetch(value, data => this.setState({ data }));
  };
  render() {
    const options = malaysiaStates.map(d => (
      <Option key={d.value}>{d.text}</Option>
    ));
    return (
      <div style={{ padding: "20px" }}>
        <p>
          <b>Notes</b>: This is a demo for searching states of Malaysia in a
          drop-down.<br />
          Try to input "ke" in the text box bellow. You will get two states.
        </p>
        <Form.Item label={<b>State</b>}>
          <Select
            showSearch
            style={{ width: "400px" }}
            optionFilterProp="children"
            filterOption={(input, option) =>
              option.props.children
                .toLowerCase()
                .indexOf(input.toLowerCase()) >= 0
            }
          >
            {options}
          </Select>
        </Form.Item>
      </div>
    );
  }
}

export default SearchInStateSelect;



